.button {
  color: $first-color;

  font-weight: 700;
  font-size: 18px;
  line-height: 18px;
  text-align: center;
  text-decoration: none;

  border: 3px solid $first-color;
  border-radius: 30px;

  &--secondary {
    color: $color-white;
    border-color: $color-white;
    background-color: $second-color-secondary;
  }

  &--cancel {
    color: $button-cancel-color;
    border-color: $button-cancel-color;
    background-color: transparent;
  }
}
